import React from 'react'
import { DEFAULT_PROPS } from './config';
import "./styles/logo.css"
import logoDark from "../../../theme/img/logo/logo-black.png"
import logoLight from "../../../theme/img/logo/logo-white.png"

const Logo = (props) => {
    return(
        <a href="/" className={`logo ${props.addClass}`}>
            <img className="logo__img" src={props.isDark ? logoDark : logoLight} alt="Electronic shop logo"/>
        </a>
    );
};

Logo.defaultProps = DEFAULT_PROPS;
export default Logo;