import "./styles/button.css"
import { DEFAULT_PROPS } from "./config"

const Button = (props) => {
    return (
        props.href && (
            <a className={`button ${props.addClass}`} href={props.href} onClick={props.onClick}>
                {props.title}
            </a>
        ) || (
            <button className={`button ${props.addClass}`} onClick={props.onClick}>
                {props.title}
            </button>
        )
    );
}

Button.defaultProps = DEFAULT_PROPS;
export default Button;