import React, { useState, useEffect } from 'react';
import "./styles/productDetails.css";
import { Page } from "../../../pages";
import { useSearchParams } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { formattedNumber } from './lib/formatter';
import axios from 'axios';
import { IconButton, Breadcrumbs } from '../../../components';
import testImg from "../../../../theme/img/products/iphone-15-pro-max-256gb-blue.webp";
import addToCartIcon from "../../../../theme/img/icons/add-to-cart.png";
import { addToCart } from '../../../../store/slices/cartSlice';
import { useNavigate } from 'react-router-dom';
import { paramDictionary, unitDictionary } from './config';
import ReactMarkdown from 'react-markdown';
import { loadImage } from './lib/loadImage';
import { Helmet } from 'react-helmet';
import { toggleModal } from '../../../../store/slices/modalSlice';

const Product = () => {
    const [product, setProduct] = useState({});
    const [isLoading, setIsLoading] = useState(true);
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const imagePath = loadImage(product.imageLinks);
    const urlParams = new URLSearchParams(window.location.search);
    const productId = urlParams.get('id');

    useEffect(() => {

        const fetchData = async () => {
            await axios.get(`https://94.240.183.84:8082/api/prod/smartphones?id=${productId}`)
            .then(response => {
                if (response.data) {
                    setProduct(response.data);
                    setIsLoading(false);
                } else {
                    console.error("  : ", response.data)
                    setIsLoading(false);
                }
            })
            .catch(error => {
                console.error("      : ", error);
                setIsLoading(false);
            });
        }

        fetchData();

    }, [productId]);

    useEffect(() => {
        if (!isLoading && Object.keys(product).length === 0) {
            navigate('/not-found');
        }
      }, [isLoading, product, navigate]);

    const addToCartHandle = (product) => {
        dispatch(addToCart(product));
        dispatch(toggleModal("addProductModal"));
    }

    return(
        <Page>
            <Helmet>
                <title>{`${product.name}`}</title>
            </Helmet>
            <div className="product-page">
                <div className="conteiner">
                <Breadcrumbs addToPath={product.name} />
                    {(Object.keys(product).length !== 0 && !isLoading) && (
                        <div className="product-page__content">
                            <div className="product-page__header">

                                <div className="product-page__img">
                                    <img src={imagePath} alt={product.name} />
                                </div>

                                <div className="product-page__info">
                                    <div className="product-page__title">
                                        <h1 className="product-page__name">{product.name}</h1>
                                        <span className="product-page__articul">{product.articul}</span>
                                    </div>
                                    <div className="product-page__actions">
                                        <p className="product-page__price">{formattedNumber(product.price)}&#8372;</p>
                                        <IconButton title="" icon={addToCartIcon} onClick={() => addToCartHandle(product)} />
                                    </div>
                                </div>

                            </div>

                            <div className="product-page__details">

                                <div className="product-page__info-block">
                                    <div className="product-page__specs-table">
                                        <div className="specs-table">
                                            <p className="specs-table__title"></p>
                                            <div className="specs-table__body">
                                                {Object.keys(product).map((key) => (
                                                    paramDictionary[key] && (
                                                        <div className="specs-table__row" key={key}>
                                                            <p className="specs-table__column">{paramDictionary[key]}</p>
                                                            <p className="specs-table__column">{product[key] + unitDictionary[key]}</p>
                                                        </div>
                                                    )
                                                ))}
                                            </div>
                                        </div>
                                    </div>
                                </div>

                                <div className="product-page__info-block">
                                    <div className="product-page__desc">
                                        <p className="product-page__desc-title"></p>
                                        <p className="product-page__desc-text"><ReactMarkdown>{product.description}</ReactMarkdown></p>
                                    </div>
                                </div>

                            </div>
                        </div>
                    ) || null}

                </div>
            </div>
        </Page>
    )
}

Product.displayName = `/product`;
export default Product;