import React from "react";
import "./styles/cartProduct.css";
import testImg from "../../../theme/img/products/iphone-15-pro-max-256gb-blue.webp";
import { ProductCounter } from "../";
import { addToCart, removeFromCart, subtractItemFromCart } from "../../../store/slices/cartSlice";
import { useDispatch } from "react-redux";
import { formattedNumber } from "./lib/formatter";
import { loadImage } from './lib/loadImage';

const CartProduct = (props) => {
    const dispatch = useDispatch();
    const imagePath = loadImage(props.item.product.imageLinks);

    const addToCardWithDispatch = (item) => {
        dispatch(addToCart(item));
    }

    const subtractWithDispatch = (item) => {
        if(item.quantity > 1) {
            dispatch(subtractItemFromCart(item.product))
        } else {
            dispatch(removeFromCart(item.product));
        }
    }

    return(
        <div className="cart-product">
            <div className="cart-product__item">
                <div className="cart-product__info">
                    <button className="cart-product__cancel" onClick={props.onClick} />
                    <a className="cart-product__img" href={`/catalog/product?id=${props.item.product.productId}`}>
                        <img src={imagePath} alt={props.item.product.name} />
                    </a>
                    <div className="cart-product__desc">
                        <a className="cart-product__title" href={`/catalog/product?id=${props.item.product.productId}`}>{props.item.product.name}</a>
                        <span className="cart-product__articul">{props.item.product.articul}</span>
                    </div>
                </div>

                <ProductCounter 
                    addClass="cart-product__product-counter" 
                    item={props.item}
                    onSubtractClick={(item) => subtractWithDispatch(item)} 
                    onAddClick={(item) => addToCardWithDispatch(item)}
                    />

                <p className="cart-product__price">
                    {formattedNumber(props.item.product.price * props.item.quantity)}
                    <span className="cart-product__currency">&#8372;</span>
                </p>
            </div>
        </div>
    )
}

export default CartProduct;