import React, { useState, useEffect } from 'react';
import fBanner from "../../../theme/img/banners/topbanner-1.jpg";
import sBanner from "../../../theme/img/banners/topbanner-2.jpg";
import tBanner from "../../../theme/img/banners/topbanner-3.jpg";
import "./styles/banner.css";

const images = [
    {
      image: fBanner,
      link: "http://localhost:3000/catalog/product?id=22"
    },
    {
      image: sBanner,
      link: "http://localhost:3000/catalog/product?id=21"
    },
    {
      image: tBanner,
      link: "http://localhost:3000/catalog/product?id=4"
    }
  ];

const Banner = () => {
  const [index, setIndex] = useState(0);
  const [isHovered, setIsHovered] = useState(false);

  useEffect(() => {
    if (!isHovered) {
      const interval = setInterval(() => {
        setIndex((prevIndex) => (prevIndex + 1) % images.length);
      }, 3000);

      return () => clearInterval(interval);
    }
  }, [isHovered]);

  const goToSlide = (index) => {
    setIndex(index);
  };

  return (
    <div
      className="banner"
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
    >
      <div className="banner__slides" style={{ transform: `translateX(-${index * 100}%)` }}>
        {images.map((src, i) => (
          <a className="banner__slide" href={src.link} key={i}>
            <img className="banner__image" src={src.image} alt={`Slide ${i}`} />
          </a>
        ))}
      </div>
      <div className="banner__dots">
        {images.map((_, i) => (
          <span
            key={i}
            className={`banner__dot ${i === index ? 'banner__dot--active' : ''}`}
            onClick={() => goToSlide(i)}
          />
        ))}
      </div>
    </div>
  );
};

export default Banner;
