import React, { useState } from "react";
import "./styles/regModalForm.css";
import { Modal, Input, Button, Submit } from "../../components";
import { useSelector, useDispatch  } from "react-redux";
import { closeModal } from "../../../store/slices/modalSlice";
import { isStrongPassword, isValidEmail, isValidName } from "./lib/validateForm";

const RegModalForm = (props) => {
    const dispatch = useDispatch();
    const isActive = useSelector(state => state.modalWindow.regModal);
    const [firstname, setFirstname] = useState("");
    const [lastname, setLastname] = useState("");
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [confirmPassword, setConfirmPassword] = useState("");
    const [regError, setRegError] = useState("");
    

    const handleSubmit = async (event) => {
        event.preventDefault();
      
        const userData = {
            firstname,
            lastname,
            email,
            password
        };

        if (!isValidName(firstname) || !isValidName(lastname)) {
            setRegError("'       2 !");
            return;
        }

        if (!isValidEmail(email)) {
            setRegError('  Email!');
            return;
        }

        if (password !== confirmPassword) {
            setRegError('  !');
            return;
        }

        if (!isStrongPassword(password)) {
            setRegError('    8 , 1 , 1 , 1   1 !');
            return;
        }
        
        try {
            const res = await fetch('https://94.240.183.84:8082/api/auth/reg', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(userData)
          });

          if (!res.ok) {
            throw new Error('Network response was not ok');
          }

          const data = await res.json();
          if (data.error === '1x001') {
                setRegError("   Email  !");
            } else {
                setRegError("");
                dispatch(closeModal());
            }
        } catch (error) {
          console.error('   :', error.message);
          setRegError(error.message);
        }
      };

    return(
        isActive && (
            <Modal>
                <form className="reg-form" onSubmit={handleSubmit}>
                    <div className="reg-form__body">

                        <div className="reg-form__some">
                            <div className="reg-form__input">
                                <label className="reg-form__field-label" htmlFor="firstname">':</label>
                                <div className="reg-form__field">
                                    <Input id="firstname" value={firstname} onChange={(e) => setFirstname(e.target.value)} type="text" placeholder="John" />
                                </div>
                            </div>

                            <div className="reg-form__input">
                                <label className="reg-form__field-label" htmlFor="lastname">:</label>
                                <div className="reg-form__field">
                                    <Input id="lastname" value={lastname} onChange={(e) => setLastname(e.target.value)} type="text" placeholder="Doe" />
                                </div>
                            </div>
                        </div>

                        <div className="reg-form__input">
                            <label className="reg-form__field-label" htmlFor="email">E-mail :</label>
                            <div className="reg-form__field">
                                <Input id="email" value={email} onChange={(e) => setEmail(e.target.value)} type="text" placeholder="example@gmail.com" />
                            </div>
                        </div>

                        <div className="reg-form__input">
                            <label className="reg-form__field-label" htmlFor="password">:</label>
                            <div className="reg-form__field">
                                <Input id="password" value={password} onChange={(e) => setPassword(e.target.value)} type="password" placeholder="8+ characters (at least 1 letter & 1 number)" />
                            </div>
                        </div>

                        <div className="reg-form__input">
                            <label className="reg-form__field-label" htmlFor="confirmPassword"> :</label>
                            <div className="reg-form__field">
                                <Input id="confirmPassword" value={confirmPassword} onChange={(e) => setConfirmPassword(e.target.value)} type="password" placeholder=" " />
                            </div>
                        </div>

                    </div>

                    {regError && (
                        <p className="reg-form__error">{regError}</p>
                    )}

                    <div className="reg-form__actions">
                        <Submit className="reg-form__submit" title="" />
                    </div>
                </form>
            </Modal>
        )
    )
}

export default RegModalForm;