export const getTotalPrice = (products) => {
    return products.reduce(
        (total, item) => total + (item.product.price * item.quantity), 0);
}

export const getProductCount = (cart) => {
    return cart.reduce((acc, item) => {
        return acc += item.quantity;
    }, 0);
}

export const getProductCountWithTitle = (cart) => {
    const productsCount = getProductCount(cart);

    const decimalRemain = productsCount % 100;
    const lastDigit = productsCount % 10;

    if (decimalRemain >= 11 && decimalRemain <= 14) {
        return `${productsCount} `;
    } else if (lastDigit == 1) {
        return `${productsCount} `;
    } else if (lastDigit >= 2 && lastDigit <= 4) {
        return `${productsCount} `;
    } else {
        return `${productsCount} `;
    }
}

export const formattedNumber = (num) => {
    const numString = num.toString();
    const [wholePart, fractionalPart] = numString.split('.');
    const formattedFractionalPart = fractionalPart?.substring(0, 2) || '';
    const formattedWholePart = wholePart.replace(/\B(?=(\d{3})+(?!\d))/g, ' ');
    return fractionalPart ? `${formattedWholePart}.${formattedFractionalPart}` : formattedWholePart;
}