import React, { useEffect, useState } from "react";
import "./styles/cart.css";
import cartIcon from "../../../theme/img/icons/cart.png";
import emptyCartImage from "../../../theme/img/icons/empty-cart.png";
import { CartProduct, Button } from "../";
import { useSelector, useDispatch } from "react-redux";
import { removeFromCart, clearCart } from "../../../store/slices/cartSlice";
import { getTotalPrice, getProductCount, getProductCountWithTitle, formattedNumber } from "./lib/cartFunc";

const Cart = () => {
    const cart = useSelector(state => state.cart.items);
    const dispatch = useDispatch();

    return(
        <div className="cart">
            <img className="cart__img" src={cartIcon} alt=" " />
            {cart.length > 0 && (
                <div className="cart__label">
                    <span className="cart__counter">{getProductCount(cart) > 99 ? "99+" : getProductCount(cart)}</span>
                </div>
            )}

            <div className="cart__dropdown">
                <div className="cart__desc">
                    {cart.length > 0 && (
                        <p className="cart__stats">
                              
                            <span className="cart__product-count"> {getProductCountWithTitle(cart)} </span> 
                               
                            <span className="cart__total-price"> {formattedNumber(getTotalPrice(cart))}&#8372;</span>
                        </p>
                    ) || (<div className="cart__empty">
                        <div className="cart__empty-img">
                            <img src={emptyCartImage} alt=" " />
                        </div>
                        <p className="cart__empty-warning">  </p>
                        <a className="cart__text-link" href="/catalog"> </a>
                    </div>)}
                </div>

                {cart.length > 0 && (
                    <div className="cart__product-list">
                        {cart
                            .map((item) => (
                                <CartProduct item={item} onClick={() => dispatch(removeFromCart(item.product))} />
                            ))}
                    </div>
                )}

                {cart.length > 0 && (
                    <div className="cart__actions">
                        <Button addClass="cart__order-button" href="/order" title=" " />
                    </div>
                )}
            </div>

        </div>
    );
}

export default Cart;