import React from "react";
import "./styles/linkList.css";

const LinkList = (props) => {
    return (
        <div className={`link-list ${props.addClass}`}>
            <p className="link-list__title">{props.title}</p>
            <ul className="link-list__list">
                {props.links.map((item) => 
                    (<li className="link-list__item">
                        {item.link && (
                            <a className="link-list__link" href={item.link}>{item.title}</a>
                        ) || (
                            <span className="link-list__text">{item.title}</span>
                        )}
                    </li>))}
            </ul>
        </div>
    )
} 

export default LinkList;