import React, { useState } from "react";
import "./styles/logoutModal.css";
import { Modal, Button } from "../../components";
import { useSelector, useDispatch } from "react-redux";
import { closeModal } from "../../../store/slices/modalSlice";
import { clearProfile } from "../../../store/slices/authSlice";

const LogoutModal = () => {
    const dispatch = useDispatch();
    const isActive = useSelector(state => state.modalWindow.logoutModal);

    const logoutHandle = () => {
        dispatch(clearProfile());
        dispatch(closeModal());
    }

    return(
        isActive && (
            <Modal>
                <div className="logout-modal">
                    <p className="logout-modal__title">       ?</p>
                    <div className="logout-modal__actions">
                        <Button title="" onClick={() => dispatch(closeModal())} />
                        <Button title="" onClick={() => logoutHandle()} />
                    </div>
                </div>
            </Modal>
        )
    )
}

export default LogoutModal;