import React, { Children } from 'react'
import "./styles/page.css"
import cart from "../../../theme/img/icons/cart.png"
import { IconButton, Logo, SearchBox, Breadcrumbs } from "../../components/"
import { Header, Footer, AuthModalForm, RegModalForm, AddProductModal, LogoutModal } from "../../blocks/"
import { useLocation } from 'react-router-dom'


const Page = ({ children }) => {
    const location = useLocation();
    const pathnames = location.pathname.split('/').filter(x => x);

    return(
        <div className="page">
            <AuthModalForm />
            <RegModalForm />
            <AddProductModal />
            <LogoutModal />
            <Header />
            <main className="main">
                {children}
            </main>
            <Footer />
        </div>
    );
}


Page.displayName = '/';
export default Page;