import React from "react";
import "./styles/featuredProducts.css"
import { Product } from "../../components";
import { addToCart } from "../../../store/slices/cartSlice";
import { useDispatch } from "react-redux";
import { DEFAULT_PROPS } from "./config";


const FeaturedProducts = (props) => {
    const dispatch = useDispatch();

    return(
        <div className={`featured-products ${props.className}`}>
            <div className="conteiner">

                <div className="featured-products__header">
                    <h2 className="featured-products__title">{props.title}</h2>
                    <a className="featured-products__link" href="/catalog"> </a>
                </div>

                <div className="featured-products__list">
                    {console.log(props.data)}
                    {props.data.map((product) => (
                        <div className="featured-products__list-item">
                            <Product item={product} onClick={() => dispatch(addToCart(product))} />
                        </div>
                    ))}
                </div>

            </div>
        </div>
    )
}

FeaturedProducts.defaultProps = DEFAULT_PROPS;
export default FeaturedProducts;