import React, { useState } from 'react';
import "./styles/authModalForm.css";
import { Modal, Input, Button, Submit } from "../../components";
import { useDispatch, useSelector } from "react-redux";
import { toggleModal, closeModal } from "../../../store/slices/modalSlice";
import { setProfile } from "../../../store/slices/authSlice";

const AuthModalForm = (props) => {
    const dispatch = useDispatch();
    const isActive = useSelector(state => state.modalWindow.authModal);
    const [login, setLogin] = useState('');
    const [password, setPassword] = useState('');
    const [authError, setAuthError] = useState("");

    const handleSubmit = async (event) => {
        event.preventDefault();
      
        const formData = new FormData();
        formData.append('email', login);
        formData.append('password', password);
        
        try {
            const res = await fetch('https://94.240.183.84:8082/api/auth/login', {
            method: 'POST',
            body: formData,
          });

          if (!res.ok) {
            throw new Error('Network response was not ok');
          }

          const data = await res.json();
          if (data.error === '1x002') {
                setAuthError("  !    .");
            } else {
                setAuthError("");
                dispatch(setProfile(data));
                dispatch(closeModal());
            }
        } catch (error) {
          console.error('   :', error.message);
        }
      };
      

    return(
        isActive && (
            <Modal>
                <form className="auth-form" onSubmit={handleSubmit}>
                    <div className="auth-form__body">

                        <div className="auth-form__input">
                            <label className="auth-form__field-label" htmlFor="login">:</label>
                            <div className="auth-form__field">
                                <Input id="login" value={login} onChange={(e) => setLogin(e.target.value)} placeholder="example@gmail.com" />
                            </div>
                        </div>

                        <div className="auth-form__input">
                            <label className="auth-form__field-label" htmlFor="password">:</label>
                            <div className="auth-form__field">
                                <Input id="password" value={password} onChange={(e) => setPassword(e.target.value)} type="password" placeholder=" " />
                            </div>
                        </div>

                    </div>

                    {authError && (
                        <p className="auth-form__error">{authError}</p>
                    )}

                    <div className="auth-form__actions">
                        <Submit className="auth-form__submit" title="" />
                        <button className="auth-form__link" onClick={() => dispatch(toggleModal("regModal"))} > ?</button>
                    </div>
                </form>
            </Modal>
        )
    )
}

export default AuthModalForm;