import React from 'react';
import { DEFAULT_PROPS } from './config';
import "./styles/searchBox.css";

const SearchBox = (props) => {
    
    return(
        <div className={`search-box ${props.addClass}`}>
            <form className="search-box__form" action={`http://localhost:3000/catalog`}>
                <input className="search-box__field" type="search" name="search" placeholder={props.placeholder} />
                <input className="search-box__submit" type="submit" value="" />
            </form>
        </div>
    );
}

SearchBox.defaultProps = DEFAULT_PROPS;
export default SearchBox;