import "./styles/submit.css"
import { DEFAULT_PROPS } from "./config"

const Submit = (props) => {
    return (
        <input 
            className={`submit ${props.className}`} 
            onClick={props.onClick} 
            type="submit" 
            value={props.title} 
        />
    );
}

Submit.defaultProps = DEFAULT_PROPS;
export default Submit;