export const formattedNumber = (num) => {
    const numString = num.toString();
    const [wholePart, fractionalPart] = numString.split('.');
    const formattedWholePart = wholePart.replace(/\B(?=(\d{3})+(?!\d))/g, ' ');
    return fractionalPart ? `${formattedWholePart}.${fractionalPart}` : formattedWholePart;
}