import React, { useEffect } from "react";
import "./styles/addProductModal.css";
import { Modal, CartProduct, Button } from "../../components";
import { useSelector, useDispatch } from "react-redux";
import { removeFromCart } from "../../../store/slices/cartSlice";
import { getProductCount, formattedNumber, getTotalPrice, getProductCountWithTitle } from "../../components/Cart/lib/cartFunc";
import emptyCartImage from "../../../theme/img/icons/empty-cart.png";
import { closeModal } from "../../../store/slices/modalSlice";

const AddProductModal = () => {
    const dispatch = useDispatch();
    const cart = useSelector(state => state.cart.items);
    const isActive = useSelector(state => state.modalWindow.addProductModal);

    useEffect(() => {
        if (isActive && cart.length == 0) {
            dispatch(closeModal());
        }
    }, [isActive, cart]);

    return(
        isActive && (
            <Modal>
                <div className="add-modal">
                    {cart.length > 0 && (
                        <p className="add-modal__stats">
                              
                            <span className="add-modal__product-count"> {getProductCountWithTitle(cart)} </span> 
                               
                            <span className="add-modal__total-price"> {formattedNumber(getTotalPrice(cart))}&#8372;</span>
                        </p>
                    )}

                    {cart.length > 0 && (
                        <div className="add-modal__product-list">
                            {cart
                                .map((item) => (
                                    <CartProduct item={item} onClick={() => dispatch(removeFromCart(item.product))} />
                                ))}
                        </div>
                    )}

                    <div className="add-modal__actions">
                        <Button title=" " href="/order" onClick={() => dispatch(closeModal())} />
                        <Button title=" " onClick={() => dispatch(closeModal())} />
                    </div>
                </div>
            </Modal>
        )
    )
}

export default AddProductModal;