import React from "react";
import { Link, useLocation } from 'react-router-dom';
import "./styles/breadcrumbs.css";
import { getCurrentLocation } from "./lib/getCurrentLocation";

const routeNames = {
    "/catalog": "",
    "/delivery": "  ",
    "/catalog/product": "",
    "/about": " ",
    "/contacts": "",
    "/not-found": "  "
  };
  
  const Breadcrumbs = (props) => {
    const location = useLocation();
    const pathnames = getCurrentLocation(location, props.addToPath);
    
    return (
      <nav className="breadcrumbs">
        <ul className="breadcrumbs__list">
          <li className="breadcrumbs__list-item">
            <a className="breadcrumbs__link" href="/"></a>
          </li>
          {pathnames.map((value, index) => {
            const to = `/${pathnames.slice(0, index + 1).join('/')}`;
            const isLast = index === pathnames.length - 1;
            const name = routeNames[to] || decodeURIComponent(value);
            return (
              <li key={to} className="breadcrumbs__list-item">
                {isLast ? (
                  <span className="breadcrumbs__current">{name}</span>
                ) : (
                  <a className="breadcrumbs__link" href={to}>{name}</a>
                )}
              </li>
            );
          })}
        </ul>
      </nav>
    );
  };
  
  export default Breadcrumbs;  