import "./styles/home.css";
import React, { useState, useEffect } from 'react';
import { FeaturedProducts } from "../../../blocks";
import { Banner } from "../../../components";
import { Page } from "../../../pages";
import { Helmet } from "react-helmet";
import axios from "axios";


const Home = () => {
    const [newProducts, setNewProducts] = useState();
    const [topProducts, setTopProducts] = useState();
    const [flagmanProducts, setFlagmanProducts] = useState();

    
    useEffect(() => {
        const fetchData = async () => {
            try {
                const [newResponse, topResponse, flagmanResponse] = await Promise.all([
                    axios.get(`https://94.240.183.84:8082/api/prod/smartphones/new`),
                    axios.get(`https://94.240.183.84:8082/api/prod/smartphones/top`),
                    axios.get(`https://94.240.183.84:8082/api/prod/smartphones/flagmans`)
                ]);
    
                if (Array.isArray(newResponse.data)) {
                    setNewProducts(newResponse.data);
                } else {
                    console.error("     : ", newResponse.data);
                }
    
                if (Array.isArray(topResponse.data)) {
                    setTopProducts(topResponse.data);
                } else {
                    console.error("     : ", topResponse.data);
                }
    
                if (Array.isArray(flagmanResponse.data)) {
                    setFlagmanProducts(flagmanResponse.data);
                } else {
                    console.error("     : ", flagmanResponse.data);
                }
            } catch (error) {
                console.error("    : ", error);
            }
        };
    
        fetchData();
    }, []);

    return(
        <Page>
            <Helmet>
                <title>{"E-SHOP | "}</title>
            </Helmet>
            <div className="home">
                <div className="conteiner">
                    <Banner />
                </div>
                <FeaturedProducts className="home__featured-products" title="" data={newProducts} />
                <FeaturedProducts className="home__featured-products" title=" " data={topProducts} />
                <FeaturedProducts className="home__featured-products" title="" data={flagmanProducts} />
            </div>
        </Page>
    );
};

Home.displayName = '/home';
export default Home;