import React from "react";
import "./styles/productCounter.css";

const ProductCounter = (props) => {
    return(
        <div className={`product-counter ${props.addClass}`}>
            <button className="product-counter__action product-counter__action--subtract" onClick={() => props.onSubtractClick(props.item)} />
            <span className="product-counter__quantity">{props.item.quantity}</span>
            <button className="product-counter__action product-counter__action--add" onClick={() => props.onAddClick(props.item.product)} />
        </div>
    )
}

export default ProductCounter;