import "./styles/catalog.css";
import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { useDispatch } from 'react-redux';
import { Page } from "../../../pages";
import noMatchesImage from "../../../../theme/img/icons/no-matches.png";
import { Product, FilterList, Breadcrumbs, Modal, Input } from '../../../components';
import { AddProductModal, AuthModalForm, RegModalForm } from "../../../blocks";
import { addToCart } from "../../../../store/slices/cartSlice";
import { Helmet } from "react-helmet";
import { filters } from "./config";


const Catalog = () => {
    const dispatch = useDispatch();
    const [products, setProducts] = useState([]);
    const urlParams = new URLSearchParams(window.location.search);
    const searchRequest = urlParams.get('search');

    const [selectedFilters, setSelectedFilters] = useState({
        brand: [],
        storageCapacity: [],
        ram: [],
        displaySize: [],
        resolution: []
    });

    const handleFilterChange = (filterType, value) => {
        setSelectedFilters(prevFilters => ({
            ...prevFilters,
            [filterType]: prevFilters[filterType].includes(value)
                ? prevFilters[filterType].filter(option => option !== value)
                : [...prevFilters[filterType], value]
        }));
    };

    useEffect(() => {
        if (searchRequest && Object.keys(products).length == 0) {
            axios.get(`https://94.240.183.84:8082/api/prod/smartphones?search=${searchRequest}`)
            .then(response => {
                if (Array.isArray(response.data)) {
                    setProducts(response.data);
                } else {
                    console.error("  : ", response.data)
                }
            })
            .catch(error => {
                console.error("    : ", error);
            });
        } else {
            axios.get('https://94.240.183.84:8082/api/prod/smartphones')
            .then(response => {
                if (Array.isArray(response.data)) {
                    setProducts(response.data);
                } else {
                    console.error("  : ", response.data)
                }
            })
            .catch(error => {
                console.error("    : ", error);
            });
        }
    }, []);

    return(
        <Page>
            <Helmet>
                <title>{"E-SHOP | "}</title>
            </Helmet>
            <div class="conteiner">
                <Breadcrumbs />
                <div className="catalog">
                    <h1 className="catalog__title">{searchRequest ? " " : ""}</h1>
                    <div className="catalog__content">

                        <div className='catalog__filters'>
                            {filters.map((filter) => (
                                <FilterList title={filter.title} options={filter.values} onChange={(value) => handleFilterChange(filter.attributeKey, value)} />
                            ))}
                        </div>

                        <div className="catalog__products">
                            <div className="catalog__product-list">
                                {(() => {
                                    const filteredProducts = products.filter(product => {
                                        return Object.keys(selectedFilters).every(filterKey => {
                                            if (selectedFilters[filterKey].length === 0) {
                                                return true;
                                            }
                                            return selectedFilters[filterKey].includes(product[filterKey].toString());
                                        });
                                    });

                                    if (filteredProducts.length > 0) {
                                        return filteredProducts.map(product => (
                                            <Product key={product.id} item={product} />
                                        ));
                                    } else {
                                        return (
                                            <div className="catalog__no-matches no-matches">
                                                <div className="no-matches__img">
                                                    <img src={noMatchesImage} alt="  " />
                                                </div>
                                                <p className="no-matches__title">     .</p>
                                            </div>);
                                    }
                                })()}
                            </div>
                        </div>


                    </div>
                </div>
            </div>
        </Page>
    );
};

Catalog.displayName = '/catalog';
export default Catalog;