import React, { useState, useEffect } from 'react';
import "./styles/product.css";
import { IconButton } from "../";
import itemImage from "../../../theme/img/products/iphone-15-pro-max-256gb-blue.webp";
import addToCartIcon from "../../../theme/img/icons/add-to-cart.png";
import { loadImage } from './lib/loadImage';
import { formattedNumber } from './lib/formatter';
import { useDispatch } from 'react-redux';
import { toggleModal } from '../../../store/slices/modalSlice';
import { addToCart } from '../../../store/slices/cartSlice';


const Product = (props) => {
    const dispatch = useDispatch();
    const imagePath = loadImage(props.item.imageLinks);

    const addProductToCart = () => {
        dispatch(addToCart(props.item));
        dispatch(toggleModal("addProductModal"));
    }

    return (
        <div className="product-card">
            <a className="product-card__image" href={`/catalog/product?id=${props.item.productId}`}>
                <img src={imagePath} alt={props.item.name} />
            </a>

            <a className="product-card__title" href={`/catalog/product?id=${props.item.productId}`}>{props.item.name}</a>

            <div className="product-card__bottom">
                <div className="product-card__price">
                    <span className="product-card__sum">{formattedNumber(props.item.price)}</span>
                    <span className="product-card__currency">&#8372;</span>
                </div>

                <div className="product-card__actions">
                    <IconButton title="" icon={addToCartIcon} onClick={addProductToCart} />
                </div>
            </div>
        </div>
    )
}

export default Product;