import { createSlice } from '@reduxjs/toolkit';

const initialState = {
    items: JSON.parse(localStorage.getItem('cart')) || []
};


const cartSlice = createSlice({
    name: 'cart',
    initialState,
    reducers: {
        addToCart: (state, action) => {
            console.log("addToCart");
            const existingItem = state.items.find((item) => item.product.productId === action.payload.productId);

            if (existingItem) {
                state.items = state.items.map((item) => 
                    item.product.productId === action.payload.productId 
                        ? {...item, quantity: item.quantity + 1} 
                        : item);
            } else {
                const newItem = {
                    product: action.payload,
                    quantity: 1
                };
                state.items = [...state.items, newItem];
            }

            localStorage.setItem('cart', JSON.stringify(state.items));
        },
        removeFromCart: (state, action) => {
            console.log("removeFromCart");
            state.items = state.items.filter(item => item.product.productId !== action.payload.productId);
            localStorage.setItem('cart', JSON.stringify(state.items));
        },
        clearCart: (state) => {
            state.items = [];
            localStorage.removeItem('cart');
        },
        subtractItemFromCart: (state, action) => {
            console.log("subtractItemFromCart");
            state.items = state.items.map((item) => 
                item.product.productId === action.payload.productId 
                    ? {...item, quantity: item.quantity - 1} 
                    : item);

            localStorage.setItem('cart', JSON.stringify(state.items));
        }
    }
});

export const { addToCart, removeFromCart, clearCart, subtractItemFromCart } = cartSlice.actions;

export default cartSlice.reducer;
