import "./index.css"
import { Router, Route, Routes, Navigate } from "react-router-dom";
import { Provider } from 'react-redux';
import { Home, Catalog, Delivery, About, Contacts, Product, Order, NotFound, AdminOrders } from "./views/routes/"
import { IconButton } from "./views/components/"
import store from "./store/store"
import cart from "./theme/img/icons/cart.png"
import { AdminPage } from "./views/pages";

function App() {
  return (
      <Provider store={store}>
        <Routes>

          <Route
            exact
            element={<Home />}
            path="/"
          />

          <Route
            exact
            element={<Catalog />}
            path="/catalog"
          />

          <Route
            exact
            element={<Delivery />}
            path="/delivery"
          />

          <Route
            exact
            element={<About />}
            path="/about"
          />

          <Route
            exact
            element={<Contacts />}
            path="/contacts"
          />

          <Route
            exact
            element={<Product />}
            path="/catalog/product"
          />

          <Route
            exact
            element={<Order />}
            path="/order"
          />

          <Route
            exact
            element={<NotFound />}
            path="/not-found"
          />

          <Route
            exact
            element={<AdminOrders />}
            path="/admin/orders"
          />

          <Route
            element={<Navigate to="/not-found" />}
            path="*"
          />
        
        </Routes>
      </Provider>
  );
}

export default App;
