import React, { Children } from 'react'
import "./styles/adminPage.css"
import cart from "../../../theme/img/icons/cart.png"
import { IconButton, Logo, SearchBox, Breadcrumbs } from "../../components/"
import { Header, Footer, AuthModalForm, RegModalForm, AddProductModal, LogoutModal } from "../../blocks/"
import { useLocation } from 'react-router-dom'


const AdminPage = ({ children }) => {
    const location = useLocation();
    const pathnames = location.pathname.split('/').filter(x => x);

    return(
        <div className="admin-page">
            <header className="admin-header">
                <div className="admin-header__content">
                    <p className="admin-header__title">Admin Panel</p>
                    <ul className="admin-header__nav">
                        <li className="admin-header__nav-item">
                            <a className="admin-header__nav-link" href="/admin/produts"></a>
                        </li>
                        <li className="admin-header__nav-item">
                            <a className="admin-header__nav-link" href="/admin/orders"></a>
                        </li>
                    </ul>
                </div>
            </header>
            <main className="main">
                {children}
            </main>
        </div>
    );
}


AdminPage.displayName = '/';
export default AdminPage;