import React from 'react';
import "./styles/profile.css";
import profileIco from "../../../theme/img/icons/user.png";
import { useDispatch, useSelector } from 'react-redux';
import { toggleModal } from '../../../store/slices/modalSlice';
import { clearProfile } from '../../../store/slices/authSlice';

const Profile = () => {
    const dispatch = useDispatch();
    const profile = useSelector(state => state.auth.profile);

    return(
        <div className="profile">
            {Object.keys(profile).length !== 0 && (
                <div className="profile__authorized">
                    <p className="profile__login">{profile.firstname}</p>
                    <div className="profile__actions">
                        <button className="profile__logout" onClick={() => dispatch(toggleModal("logoutModal"))}></button>
                    </div>
                </div>
            ) || (
                <div className="profile__icon" onClick={() => dispatch(toggleModal("authModal"))}>
                    <img className="profile__img" src={profileIco} alt="   " />
                </div>
            )}
        </div>
    )
}

export default Profile;