import React from 'react'
import "./styles/header.css"
import cart from "../../../theme/img/icons/cart.png"
import { Logo, SearchBox, Cart, Profile } from "../../components"
import { Link } from "react-router-dom";
import { useDispatch, useSelector } from 'react-redux';

const Header = () => {

    return(
        <header className="header">
            <div className="conteiner header__content">
                <Logo addClass="header__logo" isDark={true} />
                <SearchBox addClass="header__search-box" placeholder="..." />
                <ul className='actions-list'>

                    <li className='actions-list__item'>
                        <Profile />
                    </li>

                    <li className='actions-list__item'>
                        <Cart />
                    </li>

                </ul>
            </div>
            
            <div className="header__nav nav">
                <div className="conteiner">
                    <ul className="nav__list">

                        <li className="nav__list-item nav__list-item--ctg">
                            <a className="nav__link" href="/catalog"> </a>
                        </li>

                        <li className="nav__list-item">
                            <a className="nav__link" href="/"></a>
                        </li>

                        <li className="nav__list-item">
                            <a className="nav__link" href="/delivery">  </a>
                        </li>

                        <li className="nav__list-item">
                            <a className="nav__link" href="/about"> </a>
                        </li>

                        <li className="nav__list-item">
                            <a className="nav__link" href="/contacts"></a>
                        </li>

                    </ul>
                </div>
            </div>
        </header>
    );
}


export default Header;