import React from 'react';
import "./styles/footer.css";
import { LinkList } from "../../components";
import { links } from "./config"
import visaCardLogo from "../../../theme/img/logo/visa.png";
import mCardLogo from "../../../theme/img/logo/mastercard.png";

const Footer = () => {

    return (
        <footer className="footer">
            <div className="conteiner">
                <div className="footer__content">
                    <div className="footer__links">
                        {links.map((item) => (
                            <LinkList addClass="footer__link-list" title={item.title} links={item.links} />
                        ))}
                    </div>

                    <div className="footer__payment-wrapper">
                        <div className="footer__payment-list">

                            <div className="footer__payment-method">
                                <img src={visaCardLogo} alt="   VISA" />
                            </div>

                            <div className="footer__payment-method">
                                <img src={mCardLogo} alt="   MasterCard" />
                            </div>

                        </div>
                    </div>
                </div>
            </div>

            <div className="footer__copyright">
                <p>E-STORE &#169; 2023 - 2024, developed and designed by Davlat Chornovol</p>
            </div>
        </footer>
    );
}

export default Footer;