import React, { useState, useEffect } from "react";
import "./styles/adminOrder.css";
import { AdminPage } from "../../../pages";
import axios from "axios";

const AdminOrders = () => {
    const [items, setItems] = useState([]);
    

    useEffect(() => {

        const fetchData = async () => {
            await axios.get("https://94.240.183.84:8082/api/get/orders")
            .then(response => {
                if (response.data) {
                    setItems(response.data);
                    console.log(items)
                } else {
                    console.error("  : ", response.data)
                }
            })
            .catch(error => {
                console.error("   : ", error);
            });
        }
        
        if (items.length == 0) {
            fetchData();
        }
        

    }, [items]);

    return(
        <AdminPage>
            <div className="admin-orders">
                <div className="admin-orders__content">
                    <div className="admin-orders__table">
                        <table className="item-table">
                            <tbody className="item-table__body">
                                <tr className="item-table__row item-table__row--header">
                                    <td className="item-table__column item-table__column--header">ID</td>
                                    <td className="item-table__column item-table__column--header"></td>
                                    <td className="item-table__column item-table__column--header"></td>
                                    <td className="item-table__column item-table__column--header">ID </td>
                                    <td className="item-table__column item-table__column--header"> </td>
                                    <td className="item-table__column item-table__column--header">'</td>
                                    <td className="item-table__column item-table__column--header"></td>
                                    <td className="item-table__column item-table__column--header"></td>
                                    <td className="item-table__column item-table__column--header">Email</td>
                                    <td className="item-table__column item-table__column--header"></td>
                                    <td className="item-table__column item-table__column--header"></td>
                                    <td className="item-table__column item-table__column--header"></td>
                                </tr>
                        
                                {Object.keys(items).map((item) => ((
                                    <tr className="item-table__row">
                                        <td className="item-table__column">{item.orderId}</td>
                                        <td className="item-table__column">fgd</td>
                                        <td className="item-table__column">fgd</td>
                                        <td className="item-table__column">fgd</td>
                                        <td className="item-table__column">fgd</td>
                                        <td className="item-table__column">fgd</td>
                                        <td className="item-table__column">fgd</td>
                                        <td className="item-table__column">fgd</td>
                                        <td className="item-table__column">fgd</td>
                                        <td className="item-table__column"></td>
                                        <td className="item-table__column">fgd</td>
                                        <td className="item-table__column">fgd</td>
                                    </tr>
                                    )
                                ))}
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        </AdminPage>
    )
}

export default AdminOrders;