import React, { useEffect, useState } from "react";
import { Page } from "../../../pages";
import { Input, Dropdown, CartProduct, Submit } from "../../../components";
import "./styles/order.css";
import { useDispatch, useSelector } from "react-redux";
import { removeFromCart, clearCart } from "../../../../store/slices/cartSlice";
import successImage from "../../../../theme/img/icons/success.png";
import { closeModal } from "../../../../store/slices/modalSlice";
import { formattedNumber, getProductCountWithTitle, getTotalPrice } from "./lib/cartFunc";
import { isValidEmail, isValidName, isValidPhoneNumber, isStrongPassword } from "./lib/validateForm";

const Order = () => {
    const dispatch = useDispatch();
    const cart = useSelector(state => state.cart.items);
    const userId = useSelector(state => state.auth.profile.userId || 1)
    const [firstname, setFirstname] = useState("");
    const [lastname, setLastname] = useState("");
    const [phoneNumber, setPhoneNumber] = useState("");
    const [email, setEmail] = useState("");
    const [deliveryMethod, setDeliveryMethod] = useState(null);
    const [paymentMethod, setPaymentMethod] = useState("");
    const [address, setAddress] = useState("");
    const [orderError, setOrderError] = useState("");
    const [transformedCart, setTransformedCart] = useState({});
    const [isSuccess, setIsSucces] = useState(false);

    const deliveryMethodList = [
        { value: '  ', label: '  ' },
        { value: '\' ', label: '\' ' },
    ]

    const paymentMethodList = [
        { value: '  ', label: '  ' },
        { value: '  ', label: '  ' },
        { value: ' ', label: ' ' },
    ]

    const transformCart = (cart) => {
        return cart.reduce((acc, item) => {
            const existingItem = acc.find((i) => i.productId === item.product.productId);
            if (existingItem) {
                existingItem.quantity += item.quantity;
            } else {
                acc.push({ productId: item.product.productId, quantity: item.quantity });
            }
            return acc;
        }, []);
    };

    useEffect(() => {
        const proccessedCart = transformCart(cart);
        setTransformedCart(proccessedCart);
        console.log(proccessedCart);
    }, [cart])

    const handleDeliveryChange = (value) => {
        setDeliveryMethod(value);
    }

    const handlePaymentChange = (value) => {
        setPaymentMethod(value);
    }

    const handleSubmit = async (event) => {
        event.preventDefault();
      
        const userData = {
            userId,
            firstname,
            lastname,
            phoneNumber,
            email,
            deliveryMethod,
            paymentMethod,
            address,
            products: transformedCart
        };

        if (!isValidName(firstname) || !isValidName(lastname)) {
            setOrderError("'       2 !");
            return;
        }

        if (!isValidEmail(email)) {
            setOrderError('  Email!');
            return;
        }

        if (!isValidPhoneNumber(phoneNumber)) {
            setOrderError('    !');
            return;
        }
        
        try {
            const res = await fetch('https://94.240.183.84:8082/api/create/order', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(userData)
          });

          if (!res.ok) {
            throw new Error('Network response was not ok');
          }

          const data = await res.json();
          if (data.status === 'NEW') {
            dispatch(clearCart());
            setIsSucces(true);
          }

        } catch (error) {
          console.error('   :', error.message);
          setOrderError(error.message);
        }
      };

    return(
        <Page>
            <div className="order">
                <div className="conteiner">
                    {!isSuccess && (
                        <div className="order__content">
                            <div className="order__block">
                                <form className="order__order-form order-form" onSubmit={handleSubmit}>
                                    <div className="order-form__body">

                                        <div className="order-form__section">
                                            <div className="order-form__input">
                                                <label className="order-form__field-label" for="firstname">' :</label>
                                                <div className="order-form__field">
                                                    <Input id="firstname" value={firstname} onChange={(e) => setFirstname(e.target.value)} placeholder="John" />
                                                </div>
                                            </div>
                                            <div className="order-form__input">
                                                <label className="order-form__field-label" for="lastname"> :</label>
                                                <div className="order-form__field">
                                                    <Input id="lastname" value={lastname} onChange={(e) => setLastname(e.target.value)} placeholder="Doe" />
                                                </div>
                                            </div>
                                        </div>

                                        <div className="order-form__section">
                                            <div className="order-form__input">
                                                <label className="order-form__field-label" for="email">Email:</label>
                                                <div className="order-form__field">
                                                    <Input id="email" value={email} onChange={(e) => setEmail(e.target.value)} placeholder="example@gmail.com" />
                                                </div>
                                            </div>
                                            <div className="order-form__input">
                                                <label className="order-form__field-label" for="phoneNumber"> :</label>
                                                <div className="order-form__field">
                                                    <Input id="phoneNumber" value={phoneNumber} onChange={(e) => setPhoneNumber(e.target.value)} placeholder="+38 (093) 111 22 33" />
                                                </div>
                                            </div>
                                        </div>

                                        <div className="order-form__section">
                                            <div className="order-form__input">
                                                <label className="order-form__field-label" for="delivery"> :</label>
                                                <div className="order-form__field">
                                                <Dropdown id="delivery" options={deliveryMethodList} onChange={handleDeliveryChange} />
                                                </div>
                                            </div>
                                            <div className="order-form__input">
                                                <label className="order-form__field-label" for="payment"> :</label>
                                                <div className="order-form__field">
                                                <Dropdown id="payment" options={paymentMethodList} onChange={handlePaymentChange} />
                                                </div>
                                            </div>
                                        </div>

                                        {deliveryMethod && (
                                        <div className="order-form__input">
                                                <label className="order-form__field-label" for="address"> :</label>
                                                <div className="order-form__field">
                                                    <Input id="address" value={address} onChange={(e) => setAddress(e.target.value)} placeholder={deliveryMethod == "  " ? ",   " : ", , "} />
                                                </div>
                                        </div>
                                        )}

                                    </div>

                                    {orderError && (
                                        <p className="order-form__error">{orderError}</p>
                                    )}

                                    <div className="order-form__actions">
                                        <Submit className="order-form__submit" />
                                    </div>
                                </form>
                            </div>

                            <div className="order__block">
                                <div className="order__cart">
                                    {cart.length > 0 && (
                                        <p className="order__stats">
                                             
                                            <span className="order__product-count"> {getProductCountWithTitle(cart)} </span>
                                              
                                            <span className="order__total-price"> {formattedNumber(getTotalPrice(cart))}&#8372;</span>
                                        </p>
                                    ) || (
                                        <div className="order__empty-cart">
                                            <p className="order__empty-warning">  </p>
                                            <a className="order__text-link" href="/catalog">   </a>
                                        </div>
                                    )}
                                    {cart.length > 0 && (
                                        <div className="order__product-list">
                                            {cart
                                                .map((item) => (
                                                    <CartProduct item={item} onClick={() => dispatch(removeFromCart(item.product))} />
                                                ))}
                                        </div>
                                    )}
                                </div>
                            </div>
                        </div>
                    ) || (
                        <div className="order__content">
                            <div className="order__success">
                                <div className="order__success-img">
                                    <img src={successImage} alt="  !" />
                                </div>
                                <p className="order__order-status">  !</p>
                                <a className="order__text-link" href="/">   </a>
                            </div>
                        </div>
                    )}
                </div>
            </div>
        </Page>
    )
}

export default Order;